STUDIP.loadChunk = function (chunk) {
    return (function () {
        if (chunk === 'code-highlight') {
            return import(
                /* webpackChunkName: "code-highlight" */
                './chunks/code-highlight'
            ).then(({default: hljs}) => {
                return hljs
            });
        }

        if (chunk === 'tablesorter') {
            return import(
                /* webpackChunkName: "tablesorter" */
                './chunks/tablesorter'
            );
        }

        return Promise.reject(new Error('Unknown chunk'));
    }()).catch((error) => {
        console.error(`Could not load chunk ${chunk}`, error);
    });
};
